const Glider = require('../vendor/glider.min');
// eslint-disable-next-line no-new
export const applyPricingSlider = (element) =>
  // eslint-disable-next-line implicit-arrow-linebreak
  new Glider(element, {
    slidesToShow: 1,
    slidesToScroll: 1,
    itemWidth: 300,

    draggable: true,
    rewind: true,
    duration: 0.2,
    dots: '.pricing-dots',

    responsive: [
      {
        breakpoint: 600,
        settings: {
          // Set to `auto` and provide item width to adjust to viewport
          slidesToShow: 2,
          slidesToScroll: 1,
          itemWidth: 100,
          duration: 0.25,
        },
      },
    ],
  });
