const Glider = require('../vendor/glider.min');
// eslint-disable-next-line no-new
export const applyCarouselSlider = (element) =>
  // eslint-disable-next-line implicit-arrow-linebreak
  new Glider(element, {
    slidesToShow: 1,
    slidesToScroll: 1,
    itemWidth: 250,
    draggable: true,
    rewind: true,
    duration: 0.2,

    arrows: {
      prev: '.glider-prev',
      next: '.glider-next',
    },
    responsive: [
      {
        breakpoint: 520,
        settings: {
          // Set to `auto` and provide item width to adjust to viewport
          slidesToShow: 'auto',
          slidesToScroll: 'auto',
          itemWidth: 150,
          duration: 0.25,
        },
      },
    ],
  });
