require('../style/main.scss');

const { initMenu } = require('./menu-action/menu-action');

const { setSavingsResult } = require('./calculator');
const { sendEmailInput } = require('./form/sendOnlyEmailField');
const { sendContactForm } = require('./form/sendContactForm');

const { applyCarouselSlider } = require('./sliders/carousel');
const { applyPricingSlider } = require('./sliders/pricing');

const contactForm = document.getElementById('contactForm');
const fillEmailForm = document.getElementById('fillEmailForm');

const guideRange = document.getElementById('guide-range');
const averageCost = document.getElementById('avg-cost');

const pricingSlider = document.querySelector('.pricing-glider');
const carouselSlider = document.querySelector('.glider');

initMenu();

pricingSlider && applyPricingSlider(pricingSlider);

carouselSlider && applyCarouselSlider(carouselSlider);

guideRange && averageCost && setSavingsResult();
guideRange &&
  averageCost &&
  guideRange.addEventListener('input', setSavingsResult);
guideRange &&
  averageCost &&
  averageCost.addEventListener('input', setSavingsResult);

fillEmailForm && fillEmailForm.addEventListener('submit', sendEmailInput);

contactForm && contactForm.addEventListener('submit', sendContactForm);
