/* eslint-disable no-unused-expressions */
/* eslint-disable operator-linebreak */

const menuLines = document.getElementById('menu-lines');

const showMenu = (clickEvent) => {
  clickEvent.stopPropagation();
  const { parentElement, children } = clickEvent.target;
  parentElement.classList.toggle('is-showing');

  [...children].forEach((element) => {
    element.classList.toggle('is-showing');
  });
};

const hideMenu = () => {
  menuLines.parentElement.classList.remove('is-showing');
  [...menuLines.children].forEach((element) => {
    element.classList.remove('is-showing');
  });
};

const listenToCloseMenu = (clickEvent) => {
  clickEvent.stopPropagation();
  const navbar = document.querySelector('.nav');
  const clickedElement = clickEvent.target;
  if (
    navbar.className.includes('is-showing') &&
    (!clickedElement.className.includes('nav') ||
      clickedElement.className === 'nav__link')
  ) {
    hideMenu();
  }
};

const initMenu = () => {
  menuLines && menuLines.addEventListener('click', showMenu);
  window.addEventListener('click', listenToCloseMenu);
  window.addEventListener('resize', () => {
    document.documentElement.getBoundingClientRect().width > 768 && hideMenu();
  });
};

export { initMenu };
