const contactFormError = document.getElementById('contactFormError');
const contactFormOk = document.getElementById('contactFormOk');

const showSuccessMessage = () => {
  contactFormOk.classList.remove('hidden');
};
const hideSuccessMessage = () => {
  contactFormOk.classList.add('hidden');
};
const showErrorMessage = () => {
  contactFormError.classList.remove('hidden');
};
const hideErrorMessage = () => {
  contactFormError.classList.add('hidden');
};

export {
  showErrorMessage,
  hideErrorMessage,
  showSuccessMessage,
  hideSuccessMessage,
};
