const localForage = require('localforage');
const { showLoader, hideLoader } = require('../loader');
const { sendEmail } = require('./sendEmail');

const {
  hideErrorMessage,
  hideSuccessMessage,
  showErrorMessage,
  showSuccessMessage,
} = require('./confirmMessages');

export function sendContactForm(submitEvent) {
  submitEvent.preventDefault();
  showLoader();
  const form = submitEvent.target;
  if (form !== null) {
    const fullname = form[0].value.trim();
    const email = form[1].value.trim();
    const phone = form[2].value.trim();

    if (!email || !fullname) {
      return;
    }
    sendEmail('service_0vw3hso', 'template_mqt13g8', {
      fullname,
      email,
      phone: phone || '',
    })
      .then(() => {
        showSuccessMessage();
      })
      .catch(() => {
        showErrorMessage();
      })
      .finally(() => {
        form.reset();
        hideLoader();
        localForage.clear();
        setTimeout(() => {
          hideErrorMessage();
          hideSuccessMessage();
        }, 3500);
      });
  } else {
    hideLoader();
  }
}
