const guideRange = document.getElementById('guide-range');
const averageCost = document.getElementById('avg-cost');
const calculatorResult = document.getElementById('calculator-result');
const guides = document.getElementById('guides');
const estimated = document.getElementById('estimated');

const getSavingsResult = (guidesNum, estimatedCost) =>
  // eslint-disable-next-line implicit-arrow-linebreak
  guidesNum * estimatedCost - guidesNum * 70;

export const setSavingsResult = () => {
  guides.textContent = ` ${guideRange.value}`;
  estimated.textContent = ` $${averageCost.value}`;
  calculatorResult.textContent = `${getSavingsResult(
    +guideRange.value,
    +averageCost.value,
  )}`;
};
