const localForage = require('localforage');
const { setEmailInputValue } = require('./setEmailInputVaue');

const { sendEmail } = require('./sendEmail');

const { EMAIL_AIBA_KEY } = require('../helpers/global');

export function sendOnlyEmailField(submitEvent) {
  submitEvent.preventDefault();
  const form = submitEvent.target;

  if (form !== null) {
    const emailInput = form[0];
    const email = form[0].value.trim();

    if (!email) {
      return;
    }

    localForage
      .setItem(EMAIL_AIBA_KEY, emailInput.value)
      .then(() => {
        setEmailInputValue();
        document.getElementById('contact').scrollIntoView({
          behavior: 'smooth',
        });
        return sendEmail('service_0vw3hso', 'template_me4keel', {
          email,
        });
      })
      .finally(() => {
        form.reset();
      });
  }
}
